import React from 'react';
import { Route, Routes } from 'react-router-dom';

const KycRoutes = React.lazy(() => import('./Kyc/Index'));
const KybRoutes = React.lazy(() => import('./Kyb/Index'));
const AccountRoutes = React.lazy(() => import('./Account/Index'));

function AoRouter() {
  return (
    <Routes>
      <Route
        path="kyc/*"
        element={(<KycRoutes />)}
      />
      <Route
        path="kyb/*"
        element={(<KybRoutes />)}
      />
      <Route
        path="account/*"
        element={(<AccountRoutes />)}
      />
    </Routes>
  );
}

export default AoRouter;
